html{
  padding: 0px 20px 20px 20px;
}

.height-10{
  height: 10px;
}

body {
  overflow: auto;
}

#dropzone{
  padding: 0.7rem;
}
.table-text-size{
  font-size: 12px;
}

.tableContainer, .selectedTableContainer{
  display: flex;
  flex-direction: column; /* stacks tables vertically */
  align-items: left; /* center tables horizontally */
  width: 100vw; /* sets the width to the viewport width */
  max-height: 100vh; /* sets the max height to the viewport height */
}

.margin-left-30{
  margin-left: 30px;
}

.flex_horizontal{
  display: flex;
}

.data-table {
  width: 90%; /* adjust as needed */
  max-height: 30vh; /* or any height you prefer */
  overflow: auto; /* add scrollbars as necessary */
  margin-top: 2rem; /* space between tables */
  /* additional table styles here */
  border:5px solid black;
}

th, td {
  padding: 3px;
}

tr:nth-child(even) {
  background-color: #D6EEEE;
}

.black_border{
  width: 400px;
  border:5px solid black;
}

.modal {
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(0,0,0,0.4);
}

.modal-content {
  background-color: #fefefe;
  /* margin: 1% auto; */
  padding: 20px;
  border: 1px solid #888;
  width: 100vw;
  overflow: auto;
}

.data-table-wrapper {
  position: relative; /* This creates a positioning context for absolutely positioned children */
}

.modal-btn {
  position: absolute; /* This positions the button relative to the nearest positioned ancestor */
  top: 0.8rem; /* Adjust as necessary */
  left: 0rem; /* Adjust as necessary */
}


.close-button {
  color: #aaa;
  float: left;
  font-size: 35px;
  font-weight: bold;
}

.close-button:hover,
.close-button:focus {
  color: black;
  text-decoration: none;
  cursor: pointer;
}